.Pagination{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 1rem;
}
  
  .Pagination button{
    width: 3rem;
    height: 3rem;
    font-family: inherit;
    font-weight: 600;
    font-size: 1.3rem;
    border-radius: .6rem;
    cursor: pointer;
    transition: all 0.3s ease;
    background: transparent;
    color: #326229;
    border-color: #fff;
    margin: 1 .7rem;
    padding: .5rem;
  }
  
  .Pagination button.active{
    font-weight: 900;
    border-color: #326229;
    background: #7c9f24;
    color: #326229;
  }