@import url("https://fonts.googleapis.com/css2?family=Cairo:wght@200;300;400;500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap");
.About .logo-photo {
  position: absolute;
  top: 100vh;
  right: 23rem;
  z-index: 13;
  width: 9rem;
}
.About .about-container {
  padding: 1rem 3rem;
}
.About .about-container .about-box {
  display: flex;
  align-items: center;
}
.About .about-container .about-box .about-info {
  text-align: right;
  line-height: 2.3rem;
  padding: 1rem 4rem;
}
.About .about-container .about-box .about-info .title {
  text-align: right;
}
.About .about-container .about-box .about-info p {
  font-size: 1.5rem;
  color: #666;
  line-height: 3rem;
}
.About .about-container .about-box img {
  width: 25rem;
  border-radius: 1rem;
}

@media screen and (max-width: 801px) {
  .About .logo-photo {
    display: none;
  }
  .About .about-container {
    padding: 1rem;
  }
  .About .about-container .about-box {
    flex-direction: column;
  }
  .About .about-container .about-box .about-info {
    line-height: 1.6rem;
    padding: 0.5rem;
  }
  .About .about-container .about-box .about-info h2 {
    font-size: 1.5rem;
  }
  .About .about-container .about-box .about-info p {
    font-size: 1.1rem;
    line-height: 1.8rem;
  }
  .About .about-container .about-box img {
    width: 15rem;
    border-radius: 0.5rem;
  }
  .About .about-container .about-box:first-child, .About .about-container .about-box:last-child {
    flex-direction: column-reverse;
  }
}/*# sourceMappingURL=About.css.map */