@import url("https://fonts.googleapis.com/css2?family=Cairo:wght@200;300;400;500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap");
.Programs {
  background: #fff;
}
.Programs .all-Programs {
  margin: 1rem 0;
  padding: 1rem;
  display: grid;
  row-gap: 1rem;
  grid-template-columns: 100%;
}
.Programs .all-Programs .Programs-item {
  display: flex;
  align-items: center;
  justify-content: center;
}
.Programs .all-Programs .Programs-item .Programs-image {
  width: 30rem;
  height: 100%;
}
.Programs .all-Programs .Programs-item .text {
  text-align: center;
  padding: 1rem;
}
.Programs .all-Programs .Programs-item .text h1 {
  font-size: 2rem;
}
.Programs .all-Programs .Programs-item .text p {
  font-size: 1.5rem;
}
.Programs .all-Programs .Programs-item:first-child h1 {
  color: #b46338;
}
.Programs .all-Programs .Programs-item:nth-child(2) {
  background: #21315a;
}
.Programs .all-Programs .Programs-item:nth-child(2) h1 {
  color: #39a5ff;
}
.Programs .all-Programs .Programs-item:nth-child(3) h1 {
  color: #cece39;
}
.Programs .all-Programs .Programs-item:nth-child(4) {
  background: #d6d6d6;
}
.Programs .all-Programs .Programs-item:nth-child(4) h1 {
  color: #687973;
}
.Programs .all-Programs .Programs-item:last-child {
  border-bottom: 0.1rem solid #356229;
}
.Programs .all-Programs .Programs-item:last-child h1 {
  color: #8ce283;
}
.Programs .all-Programs .Programs-item:first-child, .Programs .all-Programs .Programs-item:nth-child(3), .Programs .all-Programs .Programs-item:last-child {
  flex-direction: row-reverse;
}
.Programs .all-Programs .Programs-item:first-child p, .Programs .all-Programs .Programs-item:nth-child(3) p, .Programs .all-Programs .Programs-item:last-child p {
  color: #356229;
}
.Programs .all-Programs .Programs-item:nth-child(2) p, .Programs .all-Programs .Programs-item:nth-child(4) p {
  color: #fff;
}
.Programs .sub {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem 0;
}
.Programs .sub p {
  font-size: 1.3rem;
  color: #d2ced1;
}
.Programs .sub p:hover {
  color: #356229;
}
.Programs .sub img {
  width: 3rem;
  margin-left: 0.5rem;
}

@media screen and (max-width: 861px) {
  .Programs .all-Programs {
    margin: 0.6rem 0;
    padding: 0.6rem 0;
  }
  .Programs .all-Programs .Programs-item .Programs-image {
    width: 20rem;
  }
}
@media screen and (max-width: 661px) {
  .Programs .all-Programs .Programs-item {
    flex-direction: column;
  }
  .Programs .all-Programs .Programs-item .Programs-image {
    width: 100%;
  }
  .Programs .all-Programs .Programs-item .text {
    padding: 1rem;
  }
  .Programs .all-Programs .Programs-item .text h1 {
    font-size: 1.4rem;
  }
  .Programs .all-Programs .Programs-item .text p {
    font-size: 1.2rem;
  }
  .Programs .all-Programs .Programs-item:first-child, .Programs .all-Programs .Programs-item:nth-child(3), .Programs .all-Programs .Programs-item:last-child {
    flex-direction: column;
  }
  .sub p {
    font-size: 1rem;
  }
}/*# sourceMappingURL=Programs.css.map */