@import url("https://fonts.googleapis.com/css2?family=Cairo:wght@200;300;400;500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap");
.MessageForm form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.MessageForm {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.MessageForm form {
  width: 60%;
  padding: 2rem 4rem;
  border: 0.1rem solid #fff;
  background: #ddd;
  padding: 0.1rem;
  border-radius: 2rem;
  margin: 0.5rem;
  margin-bottom: 2rem;
}
.MessageForm form input,
.MessageForm form textarea {
  height: 3rem;
  width: 90%;
  outline: none;
  border: none;
  padding: 0.5rem 2rem;
  margin: 0.7rem 0;
  text-align: right;
  border-radius: 1rem;
  background: #eee;
  font-size: 1.3rem;
  color: #23442f;
}
.MessageForm form textarea {
  max-width: 70%;
  height: 7rem;
  max-height: 9rem;
  min-width: 90%;
  min-height: 7rem;
}

@media screen and (max-width: 771px) {
  .MessageForm form {
    width: 90%;
    padding: 2rem 0.5rem;
  }
  .MessageForm form input,
  .MessageForm form textarea {
    width: 100%;
    padding: 0.3rem 1rem;
    margin: 0.4rem 0;
    border-radius: 1rem;
  }
  .MessageForm form textarea {
    max-width: 100%;
    min-width: 96%;
  }
}/*# sourceMappingURL=MessageForm.css.map */