.vol-programe-card{
    width: 25rem;
    height: 17rem;
    border-radius: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    color: #fff;
    i{
        font-size: 4rem;
    }
}




@media screen and (max-width: 991px) {
    .vol-programe-card{
        margin: 0;
        margin-bottom: .5rem;
        width: 90%;
        height: 16rem;
        font-size: .9rem;
        i{
            font-size: 2rem;
        }
    }
}

@media screen and (max-width: 661px) {
    .vol-programe-card{
        margin: .4rem;
        width: 14rem;
        font-size: .7rem;
        i{
            font-size: 1.6rem;
        }
    }
}


@media screen and (max-width: 551px){
    .vol-programe-card{
        margin: 0;
        border-radius: 1rem;
        width: 99%;
        height: 13rem;
    }
}