@import '../../variables';
.About{
    .logo-photo{
        position: absolute;
        top: 100vh;
        right: 23rem;
        z-index: 13;
        width: 9rem;
    }
    .about-container{
        padding: 1rem 3rem;
        .about-box{
            display: flex;
            align-items: center;
            .about-info{
                text-align: right;
                line-height: 2.3rem;
                padding: 1rem 4rem;
                .title{
                    text-align: right;
                }
                p{
                    font-size: 1.5rem;
                    color: #666;
                    line-height: 3rem;
                }
            }
            img{
                width: 25rem;
                border-radius: 1rem;
            }
        }
    }
}


@media screen and (max-width: 801px){
    .About{
        .logo-photo{
            display: none;
        }
        .about-container{
            padding: 1rem;
            .about-box{
                flex-direction: column;
                .about-info{
                    line-height: 1.6rem;
                    padding: .5rem;
                    h2{
                        font-size: 1.5rem;
                    }
                    p{
                        font-size: 1.1rem;
                        line-height: 1.8rem;
                    }
                }
                img{
                    width: 15rem;
                    border-radius: .5rem;
                }
                &:first-child,
                &:last-child{
                    flex-direction: column-reverse;
                }
            }
        }
    }
}