@import '../../variables';

.header{
    height: 100vh;
    width: 100%;
    @extend %display-column;
    background-image: url('../../images/hero-image.png');
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    .hero{
        @extend %display-center;
        .text{
            padding: 1rem 5rem;
            text-align: center;
            @extend %display-column;
            .hero-title{
                width: fit-content;
                margin: .5rem;
                //background: linear-gradient(90deg, #134510, $light-green, #d8d458);
                font-size: 6rem;
                font-family: $font-family;
                color: #fff;
                //-webkit-background-clip: text;
                //-webkit-text-fill-color: transparent;
            }
            p{
                font-size: 1.6rem;
                padding: 1rem 0;
                text-align: right;
                color: $gray;
            }
        }
        .social-icons{
            @extend %display-column-on-justify-content;
            margin: 1rem;
            .line{
                width: .16rem;
                height: 12rem;
                background: $gray;
                border-radius: 1rem;
            }
            i{
                color: $gray;
                font-size: 1.5rem;
                margin: .5rem;
                &:hover{
                    color: $light-green;
                }
            }
        }
    }
}
@media screen and (max-width: 991px){
    .header{
        height: 100vh;
        .hero{
            .text{
                padding: .5rem;
                .hero-title{
                    font-size: 4rem;
                }
                p{
                    font-size: 1.2rem;
                    padding: 0;
                }
            }
            .social-icons{
                margin: .4rem;
                .line{
                    width: .11rem;
                    height: 10rem;
                }
                i{
                    font-size: 1.1rem;
                }
            }
        }
    }
}

@media screen and (max-width: 441px){
    .header{
        height: 80vh;
        .hero{
            .text{
                p{
                    font-size: 1rem;
                }
            }
        }
    }
}