@import '../../variables';
.BlogPage{
    @extend %display-column;
    padding: 1rem;
    text-align: right;
    margin-top: 7rem;
    img{
        width: 70%;
        height: 30rem;
    }
    span{
        font-size: 1.3rem;
        color: #888;
        margin: .5rem;
    }
    p{
        font-size: 1.5rem;
        color: #555;
        padding: 1rem 4rem;
        line-height: 4rem;
    }
    .workTeam{
        margin: 2rem 0;
        font-size: 1.6rem;
        color: $light-green;
    }
    .social-icons{
        i{
            font-size: 2.5rem;
            color: $dark-green;
            margin: .5rem 1rem;
            transition: .5s;
            &:hover{
                color: $light-green;
                transform: rotate(30deg);
            }
        }
    }
}






@media(max-width: 441px){
    .BlogPage{
        padding: .3rem;
        img{
            width: 95%;
            height: 14rem;
        }
        span{
            font-size: 1.3rem;
        }
        p{
            font-size: 1.1rem;
            padding: .5rem;
            line-height: 2rem;
        }.workTeam{
            margin: 2rem 0;
            font-size: 1.3rem;
            line-height: 3rem;
        }
        .social-icons{
            i{
                font-size: 2rem;
            }
        }
    }
}