@import '../../variables';
.our-activities{
    background: $lighter;
    padding: 1rem;
    text-align: center;
    .activities-info{
        display: grid;
        grid-template-columns: repeat(2, 50%);
        .info{
            padding: .3rem;
            display: flex;
            flex-direction: row-reverse;
            align-items: center;
            justify-content: center;
            color: #fff;
            border: .1rem solid #fff;
            margin: .4rem;
            border-radius: 3rem;
            i{
                border: .2rem solid #fff;
                padding: 1.2rem;
                border-radius: 50%;
                font-size: 3rem;
            }
            .info-text{
                padding: 0 1rem;
                text-align: right;
                h3{
                    font-size: 1.2rem;
                    color: #1c1c45;
                    text-align: center;
                }
                p{
                    font-size: 1rem;
                    line-height: 2.2rem;
                }
                span{
                    font-size: .8rem;
                    color: $gray;
                }

            }
        }
    }
    
    .volunteer-cta{
        width: 12rem;
        color: $gray;
        text-align: center;
        font-size: 1rem;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        transition: 0.5s;
        position: relative;
        left: 40%;
        span{
            font-size: 1.2rem;
            width: 0;
            overflow: hidden;
            transition: 0.5s;
        }
        &:hover{
            color: $light-green;
            span{
                margin-left: .4rem;
                width: 3.2rem;
            }
        }
    }
}


@media screen and (max-width: 700px){
    .our-activities{
        padding: 1rem .3rem;
        .activities-info{
            display: grid;
            grid-template-columns: 100%;
            .info{
                padding-top: 1rem;
                .info-text{
                    padding: 0 .3rem;
                    p{
                        font-size: 1rem;
                        line-height: 1.7rem;
                    }
                }
            }
        }
        
        .volunteer-cta{
            width: 12rem;
            color: $gray;
            text-align: center;
            font-size: 1rem;
            display: flex;
            align-items: center;
            justify-content: center;
            text-align: center;
            transition: 0.5s;
            position: relative;
            left: 40%;
            span{
                font-size: 1.2rem;
                width: 0;
                overflow: hidden;
                transition: 0.5s;
            }
            &:hover{
                color: $light-green;
                span{
                    margin-left: .4rem;
                    width: 3.2rem;
                }
            }
        }
    }
    
}