@import './variables';

*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    text-transform: capitalize;
    font-family: $Arabic-font-family;
}

body{
    background: linear-gradient(45deg, #d5d5d5, #d6d6d6);
}

.loading{
    position: relative;
    top: 50vh;
    left: 45%;
}

html{
    scroll-behavior: smooth;
}
html::-webkit-scrollbar{
    width: 0;
}

section{
    padding-top: 7rem;
}


.title{
    color: $darker;
    font-size: 2.5rem;
    margin: 1rem 0;
    text-align: center;
}

a{
    text-decoration: none;
}
li{
    list-style: none;
}

@media screen and (max-width: 700px) {
    .title{
        font-size: 1.66rem;
    }
    section{
        padding-top: 4rem;
    }
}

@media screen and (max-width: 441px) {
    .title{
        font-size: 1.2rem;
    }
}

