@import url("https://fonts.googleapis.com/css2?family=Cairo:wght@200;300;400;500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Cairo:wght@200;300;400;500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap");
form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.search-for-blog form {
  display: flex;
  flex-direction: row;
}
.search-for-blog form input {
  width: 50%;
  height: 3.4rem;
  background: #eee;
}
.search-for-blog form i {
  padding: 1rem;
  margin: 0.5rem;
  background: #356229;
  border-radius: 50%;
  color: #fff;
}
.search-for-blog form i:hover {
  background: #8cc457;
}
.search-for-blog .btn-filter {
  display: flex;
  align-items: center;
  justify-content: center;
}
.search-for-blog .btn-filter button {
  border: 0.2rem solid #8cc457;
  background: transparent;
  font-size: 1.5rem;
  color: #8cc457;
  padding: 0.5rem 2rem;
  margin: 1rem;
  border-radius: 2rem;
  position: relative;
  overflow: hidden;
  transition: 0.5s;
  z-index: 11;
}
.search-for-blog .btn-filter button:hover {
  color: #fff;
}
.search-for-blog .btn-filter button:hover::after {
  width: 100%;
}
.search-for-blog .btn-filter button::after {
  content: "";
  transition: 1s;
  position: absolute;
  left: 0;
  top: 0;
  width: 0;
  height: 100%;
  background: #8cc457;
  border-radius: 2rem;
  opacity: 0.3;
}

.all-blogs {
  display: grid;
  grid-template-columns: repeat(4, 25%);
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

form {
  padding: 2rem 4rem;
}
form input {
  height: 3rem;
  width: 70%;
  outline: none;
  border: none;
  padding: 0.5rem 2rem;
  margin: 0.7rem 0;
  text-align: right;
  border-radius: 1rem;
  background: #eee;
  font-size: 1.3rem;
  color: #23442f;
}

@media screen and (max-width: 771px) {
  form {
    padding: 2rem 0.5rem;
  }
  form input {
    width: 100%;
    padding: 0.3rem 1rem;
    margin: 0.4rem 0;
    border-radius: 1rem;
  }
}
@media screen and (max-width: 991px) {
  .all-blogs {
    grid-template-columns: repeat(3, 33%);
  }
}
@media screen and (max-width: 661px) {
  .search-for-blog form input {
    width: 100%;
    height: 3rem;
    border-radius: 2rem;
  }
  .search-for-blog form i {
    padding: 0.7rem;
  }
  .search-for-blog .btn-filter {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
  }
  .search-for-blog .btn-filter button {
    font-size: 1rem;
    padding: 0.25rem 0.5rem;
    margin: 0.5rem;
    border-radius: 1rem;
  }
  .search-for-blog .btn-filter button::after {
    border-radius: 1rem;
  }
  .all-blogs {
    grid-template-columns: repeat(2, 50%);
  }
}
@media screen and (max-width: 381px) {
  .all-blogs {
    grid-template-columns: 100%;
  }
}/*# sourceMappingURL=Blogs.css.map */