.VolunteerPrograms{
    display: grid;
    grid-template-columns: repeat(3, auto);
    gap: 1rem;
    padding: 1rem;
}

@media screen and (max-width: 951px) {
    .VolunteerPrograms{
        grid-template-columns: repeat(2, 50%);
        gap: .3rem;
        padding: .2rem;
    }
}

@media screen and (max-width: 551px) {
    .VolunteerPrograms{
        grid-template-columns: 100%;
    }
}
