.vol-programe-card {
  width: 25rem;
  height: 17rem;
  border-radius: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  color: #fff;
}
.vol-programe-card i {
  font-size: 4rem;
}

@media screen and (max-width: 991px) {
  .vol-programe-card {
    margin: 0;
    margin-bottom: 0.5rem;
    width: 90%;
    height: 16rem;
    font-size: 0.9rem;
  }
  .vol-programe-card i {
    font-size: 2rem;
  }
}
@media screen and (max-width: 661px) {
  .vol-programe-card {
    margin: 0.4rem;
    width: 14rem;
    font-size: 0.7rem;
  }
  .vol-programe-card i {
    font-size: 1.6rem;
  }
}
@media screen and (max-width: 551px) {
  .vol-programe-card {
    margin: 0;
    border-radius: 1rem;
    width: 99%;
    height: 13rem;
  }
}/*# sourceMappingURL=VolunteerProgramCard.css.map */