@import '../../variables';
.search-for-blog{
    form{
        display: flex;
        flex-direction: row;
        input{
            width: 50%;
            height: 3.4rem;
            background: #eee;
        }
        i{
            padding: 1rem;
            margin: .5rem;
            background: $dark-green;
            border-radius: 50%;
            color: #fff;
            &:hover{
                background: $light-green;
            }
        }
    }
    .btn-filter{
        display: flex;
        align-items: center;
        justify-content: center;
        button{
            border: .2rem solid $light-green;
            background: transparent;
            font-size: 1.5rem;
            color: $light-green;
            padding: .5rem 2rem;
            margin: 1rem;
            border-radius: 2rem;
            position: relative;
            overflow: hidden;
            transition: .5s;
            z-index: 11;
            &:hover{
                color: #fff;
                &::after{
                    width: 100%;
                }
            }
            &::after{
                content: "";
                transition: 1s;
                position: absolute;
                left: 0;
                top: 0;
                width: 0;
                height: 100%;
                background: $light-green;
                border-radius: 2rem;
                opacity: .3;
            }
        }
    }

}

.all-blogs{
    display: grid;
    grid-template-columns: repeat(4, 25%);
}

@import '../../variables';


form{
    @extend %display-column;
    padding: 2rem 4rem;
    input{
        height: 3rem;
        width: 70%;
        outline: none;
        border: none;
        padding: .5rem 2rem;
        margin: .7rem 0;
        text-align: right;
        border-radius: 1rem;
        background: #eee;
        font-size: 1.3rem;
        color: $darker;
    }
}
    
@media screen and (max-width: 771px){
    form{
        padding: 2rem .5rem;
        input{
            width: 100%;
            padding: .3rem 1rem;
            margin: .4rem 0;
            border-radius: 1rem;
        }
    }
}



@media screen and (max-width: 991px) {
    .all-blogs{
        grid-template-columns: repeat(3, 33%);
    }
    
}

@media screen and (max-width: 661px) {
    .search-for-blog{
        form{
            input{
                width: 100%;
                height: 3rem;
                border-radius: 2rem;
            }
            i{
                padding: .7rem;
            }
        }
        .btn-filter{
            display: flex;
            align-items: center;
            justify-content: center;
            flex-wrap: wrap;
            button{
                font-size: 1rem;
                padding: .25rem .5rem;
                margin: .5rem;
                border-radius: 1rem;
                &::after{
                    border-radius: 1rem;
                }
            }
        }
    
    }
    .all-blogs{
        grid-template-columns: repeat(2, 50%);
    }
}

@media screen and (max-width: 381px) {
    .all-blogs{
        grid-template-columns: 100%;
    }
}
