@import url("https://fonts.googleapis.com/css2?family=Cairo:wght@200;300;400;500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap");
.vol .vol-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.vol .vol-container {
  padding: 1rem;
}
.vol .vol-container img {
  width: 70%;
  height: 25rem;
}
.vol .vol-container .con .content-box {
  padding: 2rem 4rem;
  text-align: right;
  line-height: 4rem;
}
.vol .vol-container .con .content-box h4 {
  font-size: 1.3rem;
  margin: 2rem 0;
  color: #356229;
  line-height: 2rem;
}
.vol .vol-container .con .content-box p {
  font-size: 1.5rem;
}
.vol .vol-container .con .content-box ul li {
  line-height: 3rem;
  font-size: 1.6rem;
  color: #666;
  display: flex;
  flex-direction: row-reverse;
}
.vol .vol-container .con .content-box ul li span {
  margin: 0 1rem;
}

@media screen and (max-width: 700px) {
  .vol .vol-container {
    padding: 0;
    display: grid;
    grid-template-columns: 100%;
    align-items: center;
    justify-items: center;
  }
  .vol .vol-container img {
    width: 95%;
    height: 15rem;
  }
  .vol .vol-container .con .content-box {
    padding: 1rem;
    line-height: 2rem;
  }
  .vol .vol-container .con .content-box h4 {
    font-size: 1rem;
    margin: 1rem 0;
  }
  .vol .vol-container .con .content-box p {
    font-size: 1.1rem;
  }
  .vol .vol-container .con .content-box ul li {
    line-height: 2rem;
    font-size: 1rem;
  }
}/*# sourceMappingURL=Volunteer.css.map */