@import '../../variables';
.idea-content{
    background: linear-gradient(140deg, $dark-green 50%, $light-green);
    padding: .5rem 0;
    .about-idea{
        text-align: center;
        color: $light-green;
        font-size: 1.6rem;
        padding: .5rem 0;
        margin: .5rem 0;
    }
    .website-idea{
        display: flex;
        align-items: center;
        justify-content: space-around;
        gap: .7rem;
        padding-top: .5rem;
        .idea{
            display: flex;
            align-items: center;
            justify-content: space-around;
            flex-direction: column;
            h1{
                color: $light-green;
                font-size: 4rem;
                font-family: $font-family;
                letter-spacing: .6rem;
            }
            img{
                width: 9rem;
                height: 9rem;
                border-radius:  50% .4rem 50% 50%;
            }
            h2{
                text-align: center;
                font-size: 1.5rem;
                padding: .5rem 0;
                color: $gray;
            }
            p{
                color: #eee;
                font-size: .8rem;
                word-spacing: .2rem;
                padding: .4rem;
                font-weight: bold;
            }
        }
    }
}


@media screen and (max-width: 991px){
    .idea-content{
        .about-idea{
            font-size: 1.2rem;
            padding: .7rem 0;
            margin: 1rem 0;
        }
        .website-idea{
            display: flex;
            align-items: center;
            justify-content: space-around;
            flex-wrap: wrap;
            gap: .5rem;
            padding-top: .5rem;
            .idea{
                h1{
                    
                    font-size: 4rem;
                    font-weight: bold;
                    letter-spacing: .5rem;
                }
                img{
                    width: 9rem;
                    height: 9rem;
                    border-radius:  50% .1rem 50% 50%;
                }
                h2{
                    text-align: center;
                    font-size: 1.1rem;
                    padding: .5rem 0;
                }
            }
        }
    }
}

@media screen and (max-width: 441px){
    .idea-content{
        .about-idea{
            font-size: 1rem;
        }
        .website-idea{
            .idea{
                h1{
                    
                    font-size: 2.7rem;
                }
                img{
                    width: 7rem;
                    height: 7rem;
                }
            }
        }
    }
}