@import url("https://fonts.googleapis.com/css2?family=Cairo:wght@200;300;400;500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap");
.about-main .about-box {
  display: flex;
  align-items: center;
  justify-content: center;
}

.about-main .about-box {
  flex-direction: row-reverse;
  text-align: right;
  padding: 2rem;
  padding-top: 3rem;
}
.about-main .about-box img {
  width: 18rem;
  border-radius: 0.3rem;
  box-shadow: -2rem -2rem 0 #356229;
}
.about-main .about-box .about-info {
  padding: 1rem 5rem;
}
.about-main .about-box .about-info p {
  font-size: 1.5rem;
  line-height: 3rem;
  color: #356229;
}

@media screen and (max-width: 700px) {
  .about-main .about-box {
    flex-direction: column;
    padding: 0.5rem;
    padding-top: 4rem;
  }
  .about-main .about-box img {
    width: 10rem;
    border-radius: 0.3rem;
    box-shadow: -1rem -1rem 0 #356229;
  }
  .about-main .about-box .about-info {
    padding: 1rem;
  }
  .about-main .about-box .about-info h2 {
    font-size: 1.5rem;
  }
  .about-main .about-box .about-info p {
    font-size: 1.2rem;
    line-height: 2rem;
  }
}/*# sourceMappingURL=MainAbout.css.map */