@import url("https://fonts.googleapis.com/css2?family=Cairo:wght@200;300;400;500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap");
.card .blog-description {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0.7rem;
  overflow: hidden;
  transition: 0.5s;
  border-radius: 1.5rem;
}
.card:hover {
  transform: scale(1.06);
  box-shadow: 0 0 2rem 0.5rem rgba(85, 85, 85, 0.7058823529);
}
.card:hover img {
  border-radius: 0;
}
.card img {
  width: 100%;
  height: 15rem;
  border-radius: 1.5rem;
}
.card .blog-description {
  text-align: right;
  padding: 0.5rem;
  padding-bottom: 0;
}
.card .blog-description h1 {
  font-size: 1.4rem;
  color: #23442f;
  margin: 0.5rem;
}
.card .blog-description h2 {
  font-size: 1rem;
  color: #999;
}
.card .blog-description p {
  line-height: 2rem;
  font-size: 1.2rem;
  color: #356229;
}
.card .blog-description .sharing-icons li {
  margin: 0.5rem;
  color: #356229;
  font-size: 1.7rem;
  transition: 0.5s;
}
.card .blog-description .sharing-icons li:hover {
  transform: rotate(30deg);
  color: #8cc457;
}

@media screen and (max-width: 661px) {
  .card {
    margin: 0.3rem;
    border-radius: 1rem;
    transform: scale(0.9);
  }
  .card:hover {
    transform: scale(0.97);
  }
  .card img {
    height: 11rem;
  }
  .card .blog-description {
    padding: 0.5rem;
  }
  .card .blog-description h1 {
    font-size: 1.4rem;
  }
  .card .blog-description p {
    line-height: 1.6rem;
    font-size: 1rem;
  }
  .card .blog-description .sharing-icons li {
    font-size: 1.3rem;
  }
}/*# sourceMappingURL=BlogCard.css.map */