@import url("https://fonts.googleapis.com/css2?family=Cairo:wght@200;300;400;500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap");
.our-activities {
  background: #77ba6e;
  padding: 1rem;
  text-align: center;
}
.our-activities .activities-info {
  display: grid;
  grid-template-columns: repeat(2, 50%);
}
.our-activities .activities-info .info {
  padding: 0.3rem;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: center;
  color: #fff;
  border: 0.1rem solid #fff;
  margin: 0.4rem;
  border-radius: 3rem;
}
.our-activities .activities-info .info i {
  border: 0.2rem solid #fff;
  padding: 1.2rem;
  border-radius: 50%;
  font-size: 3rem;
}
.our-activities .activities-info .info .info-text {
  padding: 0 1rem;
  text-align: right;
}
.our-activities .activities-info .info .info-text h3 {
  font-size: 1.2rem;
  color: #1c1c45;
  text-align: center;
}
.our-activities .activities-info .info .info-text p {
  font-size: 1rem;
  line-height: 2.2rem;
}
.our-activities .activities-info .info .info-text span {
  font-size: 0.8rem;
  color: #d2ced1;
}
.our-activities .volunteer-cta {
  width: 12rem;
  color: #d2ced1;
  text-align: center;
  font-size: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  transition: 0.5s;
  position: relative;
  left: 40%;
}
.our-activities .volunteer-cta span {
  font-size: 1.2rem;
  width: 0;
  overflow: hidden;
  transition: 0.5s;
}
.our-activities .volunteer-cta:hover {
  color: #8cc457;
}
.our-activities .volunteer-cta:hover span {
  margin-left: 0.4rem;
  width: 3.2rem;
}

@media screen and (max-width: 700px) {
  .our-activities {
    padding: 1rem 0.3rem;
  }
  .our-activities .activities-info {
    display: grid;
    grid-template-columns: 100%;
  }
  .our-activities .activities-info .info {
    padding-top: 1rem;
  }
  .our-activities .activities-info .info .info-text {
    padding: 0 0.3rem;
  }
  .our-activities .activities-info .info .info-text p {
    font-size: 1rem;
    line-height: 1.7rem;
  }
  .our-activities .volunteer-cta {
    width: 12rem;
    color: #d2ced1;
    text-align: center;
    font-size: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    transition: 0.5s;
    position: relative;
    left: 40%;
  }
  .our-activities .volunteer-cta span {
    font-size: 1.2rem;
    width: 0;
    overflow: hidden;
    transition: 0.5s;
  }
  .our-activities .volunteer-cta:hover {
    color: #8cc457;
  }
  .our-activities .volunteer-cta:hover span {
    margin-left: 0.4rem;
    width: 3.2rem;
  }
}/*# sourceMappingURL=Activities.css.map */