@import '../../variables';
.Programs{
    background: #fff;
    .all-Programs{
        margin: 1rem 0;
        padding: 1rem;
        display: grid;
        row-gap: 1rem;
        grid-template-columns: 100%;
        .Programs-item{
            display: flex;
            align-items: center;
            justify-content: center;
            .Programs-image{
                width: 30rem;
                height: 100%;
            }
            .text{
                text-align: center;
                padding: 1rem;
                h1{
                    font-size: 2rem;
                }
                p{
                    font-size: 1.5rem;
                }
            }
            &:first-child{ h1{color: #b46338;}}
            &:nth-child(2){background: #21315a;h1{color: #39a5ff;}}
            &:nth-child(3){ h1{color: #cece39;}}
            &:nth-child(4){background: #d6d6d6; h1{color: #687973;}}
            &:last-child{ h1{color: #8ce283;} border-bottom: .1rem solid $dark-green;}

            &:first-child, &:nth-child(3),&:last-child{ flex-direction: row-reverse; p{ color: $dark-green;}}
            &:nth-child(2),&:nth-child(4){p{ color: #fff;}
        }
    }
    }
    .sub{
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 1rem 0;
        p{
            font-size: 1.3rem;
            color: $gray;
            &:hover{ color: $dark-green;}
        }
        img{
            width: 3rem;
            margin-left: .5rem;
        }
    }
}


@media screen and (max-width: 861px){
    .Programs{
        .all-Programs{
            margin: .6rem 0;
            padding: .6rem 0;
            .Programs-item{
                .Programs-image{
                    width: 20rem;
                }
            }
        }
    }
}
@media screen and (max-width: 661px){
    .Programs{
        .all-Programs{
            .Programs-item{
                flex-direction: column;
                .Programs-image{
                    width: 100%;
                }
                .text{
                    padding: 1rem;
                    h1{
                        font-size: 1.4rem;
                    }
                    p{
                        font-size: 1.2rem;
                    }
                }
                &:first-child, &:nth-child(3),&:last-child{ flex-direction: column;}
            }
        }
    }
    .sub{
        p{
            font-size: 1rem;
        }
    }
}