@import '../../variables';

.vol{
    .vol-container{
        padding: 1rem;
        @extend %display-column-on-justify-content;
        img{
            width: 70%;
            height: 25rem;
        }
        .con{
            .content-box{
                padding: 2rem 4rem;
                text-align: right;
                line-height: 4rem;
                h4{
                    font-size: 1.3rem;
                    margin: 2rem 0;
                    color: $dark-green;
                    line-height: 2rem;
                }
                p{
                    font-size: 1.5rem;
                }
                ul{
                    li{
                        line-height: 3rem;
                        font-size: 1.6rem;
                        color: #666;
                        display: flex;
                        flex-direction: row-reverse;
                        span{
                            margin: 0 1rem;
                        }
                    }
                }
            }
        }
    }
}


@media screen and (max-width: 700px){
    .vol{
        .vol-container{
            padding: 0;
            display: grid;
            grid-template-columns: 100%;
            align-items: center;
            justify-items: center;
            img{
                width: 95%;
                height: 15rem;
            }
            .con{
                .content-box{
                    padding: 1rem;
                    line-height: 2rem;
                    h4{
                        font-size: 1rem;
                        margin: 1rem 0;
                    }
                    p{
                        font-size: 1.1rem;
                    }
                    ul{
                        li{
                            line-height: 2rem;
                            font-size: 1rem;
                        }
                    }
                }
            }
        }
    }
}