@import url("https://fonts.googleapis.com/css2?family=Cairo:wght@200;300;400;500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap");
.BlogPage {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.BlogPage {
  padding: 1rem;
  text-align: right;
  margin-top: 7rem;
}
.BlogPage img {
  width: 70%;
  height: 30rem;
}
.BlogPage span {
  font-size: 1.3rem;
  color: #888;
  margin: 0.5rem;
}
.BlogPage p {
  font-size: 1.5rem;
  color: #555;
  padding: 1rem 4rem;
  line-height: 4rem;
}
.BlogPage .workTeam {
  margin: 2rem 0;
  font-size: 1.6rem;
  color: #8cc457;
}
.BlogPage .social-icons i {
  font-size: 2.5rem;
  color: #356229;
  margin: 0.5rem 1rem;
  transition: 0.5s;
}
.BlogPage .social-icons i:hover {
  color: #8cc457;
  transform: rotate(30deg);
}

@media (max-width: 441px) {
  .BlogPage {
    padding: 0.3rem;
  }
  .BlogPage img {
    width: 95%;
    height: 14rem;
  }
  .BlogPage span {
    font-size: 1.3rem;
  }
  .BlogPage p {
    font-size: 1.1rem;
    padding: 0.5rem;
    line-height: 2rem;
  }
  .BlogPage .workTeam {
    margin: 2rem 0;
    font-size: 1.3rem;
    line-height: 3rem;
  }
  .BlogPage .social-icons i {
    font-size: 2rem;
  }
}/*# sourceMappingURL=BlogPage.css.map */