@import '../../variables';
.card{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: .7rem;
    overflow: hidden;
    transition: .5s;
    border-radius: 1.5rem;
    &:hover{
        transform: scale(1.06);
        box-shadow: 0 0 2rem .5rem #555555b4;
        img{
            border-radius: 0;
        }
    }
    img{
        width: 100%;
        height: 15rem;
        border-radius: 1.5rem;
    }
    .blog-description{
        @extend %display-column;
        text-align: right;
        padding: .5rem;
        padding-bottom: 0;
        h1{
            font-size: 1.4rem;
            color: $darker;
            margin: .5rem;
        }
        h2{
            font-size: 1rem;
            color: #999;
        }
        p{
            line-height: 2rem;
            font-size: 1.2rem;
            color: $dark-green;
        }
        .sharing-icons{
            li{
                margin: .5rem;
                color: $dark-green;
                font-size: 1.7rem;
                transition: .5s;
                &:hover{
                    transform: rotate(30deg);
                    color: $light-green;
                }
            }
        }
    }
}

@media screen and (max-width: 661px){
    .card{
        margin: .3rem;
        border-radius: 1rem;
        transform: scale(.9);
        &:hover{
            transform: scale(.97);
        }
        img{
            height: 11rem;
        }
        .blog-description{
            padding: .5rem;
            h1{
                font-size: 1.4rem;
            }
            p{
                line-height: 1.6rem;
                font-size: 1rem;
            }
            .sharing-icons{
                li{
                    font-size: 1.3rem;
                }
            }
        }
    }
}