@import '../../variables';
footer{
    height: 110%;
    width: 100%;
    background-image: linear-gradient(-120deg,#63b95274, #4a83ce85 ), url('../../images/footer.png');
    background-size: cover;
    background-position: center;
    overflow: hidden;
    border-radius: 5rem 0 0 0;
    display: flex;
    flex-direction: column;
    .row{
        display: flex;
        justify-content: space-between;
        padding: 2rem;
        .col{
            @extend %display-column-on-justify-content;
            h3{
                width: fit-content;
                color: #fff;
                font-size: 1.5rem;
                .underline{
                    width: 100%;
                    height: .3rem;
                    background: $gray;
                    border-radius: .3rem;
                    position: relative;
                    margin: .5rem 0;
                    overflow: hidden;
                    span{
                        width: 2rem;
                        height: 100%;
                        background: $light-green;
                        border-radius: .3rem;
                        position: absolute;
                        top: 0;
                        left: 1rem;
                        animation: moving 2s linear infinite;
                    }
                }
            }
            p{
                font-size: 1.3rem;
                color: $dark-green;
                line-height: 2rem;
                text-align: right;
            }
            i{
                font-size: 1.8rem;
                color: $dark-green;
                margin: .5rem 0;
                cursor: pointer;
                &:hover{
                    transform: scale(1.2);
                }
            }
            a{
                font-size: 1.3rem;
                margin: .2rem;
                color: #fff;
                &:hover{
                    color: $light-green;
                    transform: scale(1.2);
                }
            }
            form{
                background: $gray;
                height: 4rem;
                display: flex;
                flex-direction: row;
                align-items: center;
                border-radius: 2rem .3rem 2rem 2rem;
                padding: .5rem 1rem;
                input{
                    outline: none;
                    padding: .5rem 1rem;
                    height: 100%;
                    width: 15rem;
                    border: none;
                    background: transparent;
                }
                i, button{
                    background: transparent;
                    border: none;
                    font-size: 1.5rem;
                    padding: .5rem;
                    color: $light-green;
                    border-radius: 50%;
                }
                button:hover{
                    color: $lighter;
                    background: #0000001d;
                }
            }
        }
    }
    hr{
        width: 90%;
        border: 0;
        border-bottom: .1rem solid #fff;
        margin: 1rem auto;
    }
    .copyright{
        font-size: 1.2rem;
        text-align: center;
        color: $gray;
        font-family: $font-family;
        padding: 1rem .5rem;
        span{
            color: $darker;
            letter-spacing: .1rem;
            font-family: $font-family;
        }
    }
}

@keyframes moving{
    0%{
        left: -2rem;
    }
    100%{
        left: 100%;
    }
}

