@media screen and (max-width: 991px){
    footer{
        border-radius: 3rem 0 0 0;
        .row{
            display: grid;
            .col{
                &:first-child{grid-column: 1 / 3;}
                &:last-child{grid-column: 1 / 3;}
            }
        }
    }
}
@media screen and (max-width: 771px){}
@media screen and (max-width: 441px){
    footer{
        border-radius: 2rem 0 0 0;
        .row{
            display: flex;
            flex-wrap: wrap;
            .col{
                h3{
                    font-size: 1.3rem;
                }
                p{
                    font-size: 1.1rem;
                }
                a{
                    font-size: 1.1rem;
                }
                form{
                    input{
                        width: 100%;
                        font-size: 1.2rem;
                        padding: 0;
                    }
                }
            }
        }
    }
}