@import url("https://fonts.googleapis.com/css2?family=Cairo:wght@200;300;400;500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap");
footer .row .col {
  display: flex;
  flex-direction: column;
  align-items: center;
}

footer {
  height: 110%;
  width: 100%;
  background-image: linear-gradient(-120deg, rgba(99, 185, 82, 0.4549019608), rgba(74, 131, 206, 0.5215686275)), url("../../images/footer.png");
  background-size: cover;
  background-position: center;
  overflow: hidden;
  border-radius: 5rem 0 0 0;
  display: flex;
  flex-direction: column;
}
footer .row {
  display: flex;
  justify-content: space-between;
  padding: 2rem;
}
footer .row .col h3 {
  width: -moz-fit-content;
  width: fit-content;
  color: #fff;
  font-size: 1.5rem;
}
footer .row .col h3 .underline {
  width: 100%;
  height: 0.3rem;
  background: #d2ced1;
  border-radius: 0.3rem;
  position: relative;
  margin: 0.5rem 0;
  overflow: hidden;
}
footer .row .col h3 .underline span {
  width: 2rem;
  height: 100%;
  background: #8cc457;
  border-radius: 0.3rem;
  position: absolute;
  top: 0;
  left: 1rem;
  animation: moving 2s linear infinite;
}
footer .row .col p {
  font-size: 1.3rem;
  color: #356229;
  line-height: 2rem;
  text-align: right;
}
footer .row .col i {
  font-size: 1.8rem;
  color: #356229;
  margin: 0.5rem 0;
  cursor: pointer;
}
footer .row .col i:hover {
  transform: scale(1.2);
}
footer .row .col a {
  font-size: 1.3rem;
  margin: 0.2rem;
  color: #fff;
}
footer .row .col a:hover {
  color: #8cc457;
  transform: scale(1.2);
}
footer .row .col form {
  background: #d2ced1;
  height: 4rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  border-radius: 2rem 0.3rem 2rem 2rem;
  padding: 0.5rem 1rem;
}
footer .row .col form input {
  outline: none;
  padding: 0.5rem 1rem;
  height: 100%;
  width: 15rem;
  border: none;
  background: transparent;
}
footer .row .col form i, footer .row .col form button {
  background: transparent;
  border: none;
  font-size: 1.5rem;
  padding: 0.5rem;
  color: #8cc457;
  border-radius: 50%;
}
footer .row .col form button:hover {
  color: #77ba6e;
  background: rgba(0, 0, 0, 0.1137254902);
}
footer hr {
  width: 90%;
  border: 0;
  border-bottom: 0.1rem solid #fff;
  margin: 1rem auto;
}
footer .copyright {
  font-size: 1.2rem;
  text-align: center;
  color: #d2ced1;
  font-family: "Bebas Neue", sans-serif;
  padding: 1rem 0.5rem;
}
footer .copyright span {
  color: #23442f;
  letter-spacing: 0.1rem;
  font-family: "Bebas Neue", sans-serif;
}

@keyframes moving {
  0% {
    left: -2rem;
  }
  100% {
    left: 100%;
  }
}
@media screen and (max-width: 991px) {
  footer {
    border-radius: 3rem 0 0 0;
  }
  footer .row {
    display: grid;
  }
  footer .row .col:first-child {
    grid-column: 1/3;
  }
  footer .row .col:last-child {
    grid-column: 1/3;
  }
}
@media screen and (max-width: 441px) {
  footer {
    border-radius: 2rem 0 0 0;
  }
  footer .row {
    display: flex;
    flex-wrap: wrap;
  }
  footer .row .col h3 {
    font-size: 1.3rem;
  }
  footer .row .col p {
    font-size: 1.1rem;
  }
  footer .row .col a {
    font-size: 1.1rem;
  }
  footer .row .col form input {
    width: 100%;
    font-size: 1.2rem;
    padding: 0;
  }
}/*# sourceMappingURL=Footer.css.map */