@import '../../variables';
.white-btn{
    border: .1rem solid #fff;
    background: none;
    padding: .6rem 2rem;
    font-size: 1.5rem;
    font-family: $font-family;
    cursor: pointer;
    margin: 1rem;
    color: #fff;
    transition: .8s;
    position: relative;
    overflow: hidden;
    border-radius: 2rem;
    z-index: 40;
    &:hover{
        color: #555;
        &::before{
            height: 180%;
        }
    }
    &::before{
        content: "";
        position: absolute;
        left: 0;
        width: 100%;
        height: 0;
        background: #fff;
        border-radius: 50% 50%  0 0;
        z-index: -1;
        transition: 1s;
        bottom: 0;
    }
}

@media screen and (max-width: 661px){
    .white-btn{
        padding: .7rem 1rem;
        font-size: 1.3rem;
        margin: .6rem;
        border-radius: 1.3rem;
    }
}