@import url("https://fonts.googleapis.com/css2?family=Cairo:wght@200;300;400;500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap");
.NavBar .nav_links, .NavBar {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.NavBar {
  width: 100%;
  background: linear-gradient(to bottom, rgba(70, 69, 69, 0.467), rgba(98, 97, 97, 0.226), transparent);
  position: absolute;
  top: 0;
  padding: 0.5rem 4rem;
  z-index: 200;
}
.NavBar .logo {
  width: 4rem;
}
.NavBar .nav_links a {
  margin: 0 1rem;
  font-size: larger;
  color: #fff;
  font-weight: bold;
}
.NavBar .nav_links a:hover {
  color: #356229;
}
.NavBar .nav_links a.Volunteer-cta {
  border: 0.15rem solid #fff;
  border-radius: 2rem;
  padding: 0.5rem 2rem;
}
.NavBar .nav_links a.Volunteer-cta:hover {
  border: 0.15rem solid #356229;
}
.NavBar .fa-bars,
.NavBar .fa-xmark {
  padding: 0.5rem;
  cursor: pointer;
  color: #356229;
  font-size: 1.7rem;
  display: none;
}
.NavBar .fa-bars:hover,
.NavBar .fa-xmark:hover {
  color: #8cc457;
}

@media (max-width: 861px) {
  .NavBar {
    height: 3.5rem;
    background: rgba(0, 0, 0, 0.3);
  }
  .NavBar .logo {
    width: 3.3rem;
  }
  .NavBar .nav_links {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column-reverse;
    background: rgba(0, 0, 0, 0.3);
    -webkit-backdrop-filter: blur(1rem);
            backdrop-filter: blur(1rem);
    transition: 1s;
    transform: translateX(200vh);
    z-index: 100;
  }
  .NavBar .nav_links.active {
    transform: none;
  }
  .NavBar .nav_links a {
    margin-top: 1.5rem;
  }
  .NavBar .fa-bars,
  .NavBar .fa-xmark {
    display: flex;
  }
  .NavBar .fa-xmark {
    position: absolute;
    top: 2rem;
    left: 2rem;
  }
}/*# sourceMappingURL=NavBar.css.map */