@import url("https://fonts.googleapis.com/css2?family=Cairo:wght@200;300;400;500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap");
.header .hero {
  display: flex;
  align-items: center;
  justify-content: center;
}

.header .hero .text, .header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.header .hero .social-icons {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header {
  height: 100vh;
  width: 100%;
  background-image: url("../../images/hero-image.png");
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}
.header .hero .text {
  padding: 1rem 5rem;
  text-align: center;
}
.header .hero .text .hero-title {
  width: -moz-fit-content;
  width: fit-content;
  margin: 0.5rem;
  font-size: 6rem;
  font-family: "Bebas Neue", sans-serif;
  color: #fff;
}
.header .hero .text p {
  font-size: 1.6rem;
  padding: 1rem 0;
  text-align: right;
  color: #d2ced1;
}
.header .hero .social-icons {
  margin: 1rem;
}
.header .hero .social-icons .line {
  width: 0.16rem;
  height: 12rem;
  background: #d2ced1;
  border-radius: 1rem;
}
.header .hero .social-icons i {
  color: #d2ced1;
  font-size: 1.5rem;
  margin: 0.5rem;
}
.header .hero .social-icons i:hover {
  color: #8cc457;
}

@media screen and (max-width: 991px) {
  .header {
    height: 100vh;
  }
  .header .hero .text {
    padding: 0.5rem;
  }
  .header .hero .text .hero-title {
    font-size: 4rem;
  }
  .header .hero .text p {
    font-size: 1.2rem;
    padding: 0;
  }
  .header .hero .social-icons {
    margin: 0.4rem;
  }
  .header .hero .social-icons .line {
    width: 0.11rem;
    height: 10rem;
  }
  .header .hero .social-icons i {
    font-size: 1.1rem;
  }
}
@media screen and (max-width: 441px) {
  .header {
    height: 80vh;
  }
  .header .hero .text p {
    font-size: 1rem;
  }
}/*# sourceMappingURL=Hero.css.map */