@import '../../variables';
.about-main{
    .about-box{
        @extend %display-center;
        flex-direction: row-reverse;
        text-align: right;
        padding: 2rem;
        padding-top: 3rem;
        img{
            width: 18rem;
            border-radius: .3rem;
            box-shadow: -2rem -2rem 0 $dark-green;
        }
        .about-info{
            padding: 1rem 5rem;
            p{
                font-size: 1.5rem;
                line-height: 3rem;
                color: $dark-green;
            }
        }
    }
}


@media screen and (max-width: 700px){
    .about-main{
        .about-box{
            flex-direction: column;
            padding: .5rem;
            padding-top: 4rem;
            img{
                width: 10rem;
                border-radius: .3rem;
                box-shadow: -1rem -1rem 0 $dark-green;
            }
            .about-info{
                padding:1rem;
                h2{
                    font-size: 1.5rem;
                }
                p{
                    font-size: 1.2rem;
                    line-height: 2rem;
                }
            }
        }
    }
}