@import '../../variables';

.MessageForm{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    form{
        @extend %display-column;
        width: 60%;
        padding: 2rem 4rem;
        border: .1rem solid #fff;
        background: #ddd;
        padding: .1rem;
        border-radius: 2rem;
        margin: .5rem;
        margin-bottom: 2rem;
        input,
        textarea{
            height: 3rem;
            width: 90%;
            outline: none;
            border: none;
            padding: .5rem 2rem;
            margin: .7rem 0;
            text-align: right;
            border-radius: 1rem;
            background: #eee;
            font-size: 1.3rem;
            color: $darker;
        }
        textarea{
            max-width: 70%;
            height: 7rem;
            max-height: 9rem;
            min-width: 90%;
            min-height: 7rem;
        }
    }
    
    
}

@media screen and (max-width: 771px){
    .MessageForm{
        form{
            width: 90%;
            padding: 2rem .5rem;
            input,
            textarea{
                width: 100%;
                padding: .3rem 1rem;
                margin: .4rem 0;
                border-radius: 1rem;
            }
            textarea{
                max-width: 100%;
                min-width: 96%;
            }
        }
    }
}
