@import url('https://fonts.googleapis.com/css2?family=Cairo:wght@200;300;400;500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');

//font
$Arabic-font-family: 'Cairo', sans-serif;
$font-family: 'Bebas Neue', sans-serif;

//colors
$gray:  #d2ced1;
$darker:  #23442f;
$light-green:  #8cc457;
$lighter: #77ba6e;
$dark-green:  #356229;

%display-space{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
%display-center{
    display: flex;
    align-items: center;
    justify-content: center;
}
%display-column{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
%display-column-on-justify-content{
    display: flex;
    flex-direction: column;
    align-items: center;
}