@import url("https://fonts.googleapis.com/css2?family=Cairo:wght@200;300;400;500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap");
.idea-content {
  background: linear-gradient(140deg, #356229 50%, #8cc457);
  padding: 0.5rem 0;
}
.idea-content .about-idea {
  text-align: center;
  color: #8cc457;
  font-size: 1.6rem;
  padding: 0.5rem 0;
  margin: 0.5rem 0;
}
.idea-content .website-idea {
  display: flex;
  align-items: center;
  justify-content: space-around;
  gap: 0.7rem;
  padding-top: 0.5rem;
}
.idea-content .website-idea .idea {
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
}
.idea-content .website-idea .idea h1 {
  color: #8cc457;
  font-size: 4rem;
  font-family: "Bebas Neue", sans-serif;
  letter-spacing: 0.6rem;
}
.idea-content .website-idea .idea img {
  width: 9rem;
  height: 9rem;
  border-radius: 50% 0.4rem 50% 50%;
}
.idea-content .website-idea .idea h2 {
  text-align: center;
  font-size: 1.5rem;
  padding: 0.5rem 0;
  color: #d2ced1;
}
.idea-content .website-idea .idea p {
  color: #eee;
  font-size: 0.8rem;
  word-spacing: 0.2rem;
  padding: 0.4rem;
  font-weight: bold;
}

@media screen and (max-width: 991px) {
  .idea-content .about-idea {
    font-size: 1.2rem;
    padding: 0.7rem 0;
    margin: 1rem 0;
  }
  .idea-content .website-idea {
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
    gap: 0.5rem;
    padding-top: 0.5rem;
  }
  .idea-content .website-idea .idea h1 {
    font-size: 4rem;
    font-weight: bold;
    letter-spacing: 0.5rem;
  }
  .idea-content .website-idea .idea img {
    width: 9rem;
    height: 9rem;
    border-radius: 50% 0.1rem 50% 50%;
  }
  .idea-content .website-idea .idea h2 {
    text-align: center;
    font-size: 1.1rem;
    padding: 0.5rem 0;
  }
}
@media screen and (max-width: 441px) {
  .idea-content .about-idea {
    font-size: 1rem;
  }
  .idea-content .website-idea .idea h1 {
    font-size: 2.7rem;
  }
  .idea-content .website-idea .idea img {
    width: 7rem;
    height: 7rem;
  }
}/*# sourceMappingURL=IdeaContent.css.map */