@import '../../variables';
.NavBar{
    width: 100%;
    @extend %display-space;
    background: linear-gradient(to bottom, rgba(70, 69, 69, 0.467), rgba(98, 97, 97, 0.226), transparent);
    position: absolute;
    top: 0;
    padding: .5rem 4rem;
    z-index: 200;
    .logo{
        width: 4rem;
    }
    .nav_links{
        @extend %display-space;
        a{
            margin: 0 1rem;
            font-size: larger;
            color: #fff;
            font-weight: bold;
            &:hover{
                color: $dark-green;
            }
            &.Volunteer-cta{
                border: .15rem solid #fff;
                border-radius: 2rem;
                padding: .5rem 2rem;
                &:hover{
                    border: .15rem solid $dark-green;
                }
            }
        }
    }
    .fa-bars,
    .fa-xmark{
        padding: .5rem;
        cursor: pointer;
        color: $dark-green;
        font-size: 1.7rem;
        display: none;
        &:hover{
            color: $light-green;
        }
    }
}


@media (max-width: 861px){
    .NavBar{
        height: 3.5rem;
        background: rgba(0, 0, 0 , .3);
        .logo{
            width: 3.3rem;
        }
        .nav_links{
            position: fixed;
            top: 0;
            left: 0;
            height: 100vh;
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column-reverse;
            background: rgba(0, 0, 0 , .3);
            backdrop-filter: blur(1rem);
            transition: 1s;
            transform: translateX(200vh);
            z-index: 100;
            &.active{
                transform: none;
            }
            a{
                margin-top: 1.5rem;
            }
        }
        .fa-bars,
        .fa-xmark{
            display: flex;
        }
        .fa-xmark{
            position: absolute;
            top: 2rem;
            left: 2rem;
        }
    }
}