@import url("https://fonts.googleapis.com/css2?family=Cairo:wght@200;300;400;500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap");
.white-btn {
  border: 0.1rem solid #fff;
  background: none;
  padding: 0.6rem 2rem;
  font-size: 1.5rem;
  font-family: "Bebas Neue", sans-serif;
  cursor: pointer;
  margin: 1rem;
  color: #fff;
  transition: 0.8s;
  position: relative;
  overflow: hidden;
  border-radius: 2rem;
  z-index: 40;
}
.white-btn:hover {
  color: #555;
}
.white-btn:hover::before {
  height: 180%;
}
.white-btn::before {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 0;
  background: #fff;
  border-radius: 50% 50% 0 0;
  z-index: -1;
  transition: 1s;
  bottom: 0;
}

@media screen and (max-width: 661px) {
  .white-btn {
    padding: 0.7rem 1rem;
    font-size: 1.3rem;
    margin: 0.6rem;
    border-radius: 1.3rem;
  }
}/*# sourceMappingURL=WhiteBtn.css.map */